import React, {useState} from 'react'
import { NavLink } from 'react-router-dom'
import { FaBars } from 'react-icons/fa'
import {MdClose} from 'react-icons/md'

const Navbar = () => {
    const [showLink,setShowLink] = useState(false);

    const checkActive = (match, location) => {
        if (!location) return false;
        const { pathname } = location;
        return pathname === "/";
    }

    const toggleLink = () => {
    setShowLink(!showLink);
    }

    return (
        <nav>
            <div className="nav-toggle">
                <button className="regular-btn nav-toggle-btn" onClick={toggleLink}>
                    {showLink ? <MdClose style={{'fontSize':'1.5rem'}} /> : <FaBars />}</button>
                {showLink && (
            
                    <ul>
                        <li><NavLink to='/' className="btn">home</NavLink></li>
                        <li><NavLink to='/about' className="btn">about</NavLink></li>
                        <li><NavLink to='/services' className="btn">services</NavLink></li>
                        <li><NavLink to='/portfolio' className="btn">portfolio</NavLink></li>
                        {/* <li><NavLink to='/resume' className="btn">resume</NavLink></li> */}
                        <li><NavLink to='/testimonials' className="btn">testimonials</NavLink></li>
                        <li><NavLink to='/contact' className="btn">contact</NavLink></li>
                    </ul>
                            
                )}
            </div>
            <ul className="nav-links">
                <li><NavLink to='/' activeClassName="active-btn" isActive={checkActive} className="btn">home</NavLink></li>
                <li><NavLink to='/about' activeClassName="active-btn" className="btn">about</NavLink></li>
                <li><NavLink to='/services' activeClassName="active-btn" className="btn">services</NavLink></li>
                <li><NavLink to='/portfolio' activeClassName="active-btn" className="btn">portfolio</NavLink></li>
                <li><NavLink to='/resume' activeClassName="active-btn" className="btn">resume</NavLink></li>
                <li><NavLink to='/testimonials' activeClassName="active-btn" className="btn">testimonials</NavLink></li>
                <li><NavLink to='/contact' activeClassName="active-btn" className="btn">contact</NavLink></li>
            </ul>
        </nav >
    )
}
export default Navbar;