import React,{useState} from 'react'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import logo from '../images/portfolio/internship/branding/int-branding-1.png'
import business1 from '../images/portfolio/internship/branding/int-branding-2.png'
import business2 from '../images/portfolio/internship/branding/int-branding-3.png'
import letterhead from '../images/portfolio/internship/branding/int-branding-4.jpg'

const Branding = () => {

    const [index,setIndex] = useState(0);
    const totalSlide = 4;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }
    return (
        <div className="internship-project-container">
            {/* <h2 className="title">Branding</h2> */}
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={logo} className="project-slide-img" style={{'width':'50%'}} alt="internship-img" />
                <img src={business1} className="project-slide-img" alt="internship-img" /> 
                <img src={business2} className="project-slide-img" alt="internship-img" />  
                <img src={letterhead} className="project-slide-img" alt="internship-img" />                    
            </Carousel>
            <div className="project-btn-container internship-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
        </div>
    )
}
export default Branding;