import React,{useState} from 'react'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import campaign1 from '../images/portfolio/internship/campaign/campaign1.jpg'
import campaign2 from '../images/portfolio/internship/campaign/campaign2.jpg'
import campaign3 from '../images/portfolio/internship/campaign/campaign3.jpg'
import campaign4 from '../images/portfolio/internship/campaign/campaign4.jpg'
import campaign5 from '../images/portfolio/internship/campaign/campaign5.jpg'
import campaign6 from '../images/portfolio/internship/campaign/campaign6.jpg'
import campaign7 from '../images/portfolio/internship/campaign/campaign7.jpg'
import campaign8 from '../images/portfolio/internship/campaign/campaign8.jpg'
import campaign9 from '../images/portfolio/internship/campaign/campaign9.jpg'
import campaign10 from '../images/portfolio/internship/campaign/campaign10.jpg'
import campaign11 from '../images/portfolio/internship/campaign/campaign11.jpg'
import campaign12 from '../images/portfolio/internship/campaign/campaign12.jpg'
import campaign13 from '../images/portfolio/internship/campaign/campaign13.jpg'
import campaign14 from '../images/portfolio/internship/campaign/campaign14.jpg'

const Campaign = () => {

    const [index,setIndex] = useState(0);
    const totalSlide = 14;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }
    return (
        <div className="internship-project-container">
            {/* <h2 className="title">Branding</h2> */}
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={campaign1} className="project-slide-img" alt="internship-img" />
                <img src={campaign2} className="project-slide-img" alt="internship-img" /> 
                <img src={campaign3} className="project-slide-img" alt="internship-img" />  
                <img src={campaign4} className="project-slide-img" alt="internship-img" />     
                <img src={campaign5} className="project-slide-img" alt="internship-img" />
                <img src={campaign6} className="project-slide-img" alt="internship-img" /> 
                <img src={campaign7} className="project-slide-img" alt="internship-img" />  
                <img src={campaign8} className="project-slide-img" alt="internship-img" /> 
                <img src={campaign9} className="project-slide-img" alt="internship-img" />  
                <img src={campaign10} className="project-slide-img" alt="internship-img" />     
                <img src={campaign11} className="project-slide-img" alt="internship-img" />
                <img src={campaign12} className="project-slide-img" alt="internship-img" /> 
                <img src={campaign13} className="project-slide-img" alt="internship-img" />  
                <img src={campaign14} className="project-slide-img" alt="internship-img" />                    
            </Carousel>
            <div className="project-btn-container internship-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
        </div>
    )
}
export default Campaign;