import React,{useState} from 'react'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import event1 from '../images/portfolio/internship/event/eventday1.jpg'
import event2 from '../images/portfolio/internship/event/eventday2.jpg'
import event3 from '../images/portfolio/internship/event/eventday3.jpg'
import event4 from '../images/portfolio/internship/event/eventday4.jpg'
import event5 from '../images/portfolio/internship/event/eventday5.jpg'
// import video from '../images/portfolio/internship/event/WOTR3.mp4'

const Event = () => {

    const [index,setIndex] = useState(0);
    const totalSlide = 5;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }
    return (
        <div className="internship-project-container">
            {/* <h2 className="title">Branding</h2> */}
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={event1} className="project-slide-img" alt="internship-img" />
                <img src={event2} className="project-slide-img" alt="internship-img" /> 
                <img src={event3} className="project-slide-img" alt="internship-img" />  
                <img src={event4} className="project-slide-img" alt="internship-img" />
                <img src={event5} className="project-slide-img" alt="internship-img" /> 
                {/* <video className="internship-video" controls><source src={video}></source></video>                     */}
            </Carousel>
            <div className="project-btn-container internship-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
        </div>
    )
}
export default Event;