import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {TiPlus,TiMinus} from 'react-icons/ti'
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import webpage1 from '../images/portfolio/web/screenshots/yogadhyan/page1.png'
import webpage2 from '../images/portfolio/web/screenshots/yogadhyan/page2.png'
import webpage3 from '../images/portfolio/web/screenshots/yogadhyan/page3.png'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

const Yogadhyan = () => {

  const [showInfo, setShowInfo] = useState(false);
  const [index,setIndex] = useState(0);
  const totalSlide = 3;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }

    return (
        <div className="web-project-container">
            <header className="web-btn-container">
                <button className="web-project-btn regular-btn" onClick={()=>setShowInfo(!showInfo)}>
                    <span>Project Details</span>
                    {showInfo ? <TiMinus className="web-project-btn-icon" /> : <TiPlus className="web-project-btn-icon" />}
                </button>
                <Link to ='/portfolio/web-development-projects' className="web-close-btn regular-btn"><span>&times;</span></Link>
            </header>
            {showInfo && (
                <div className="project-details-box">
                    <div className="project-brief">
                    <h4>Project Brief :</h4>
                    <p>Designed this landing page and a unique logo for a yoga studio 'Yoga-Dhyan'. This landing page displays a fixed header contains logo, offer and contact number. Each section 
                      of this page is clipped in a polygon shape. The hero box contains video muted and autoplayed in the background, on top of it a brief importance of yoga and mediation and a call to action button 
                      to explore their programs. Next section designed to show the features of the studio on individual boxes, this boxes will scale on hover. Our courses section contains all the courses
                      with the images and caption stacked in random positions, on hover that particular image comes on top of other images with offset border. Instructors section contains 2 instructors photos 
                      clipped into an oval shape, on hover a brief info about that instructor displays on top of that image with dark opacity background. Below that there are cards with membership details on its 
                      front and on hover the card flips back and shows the fees details and a call to action button to register for membership. Below that a registeration form and class timing information. Below that student's 
                      testimonials designed in a image slider. At the footer there is a contact information, address, social media links and location map.                      
                    </p>
                </div>
                <div className="project-info">
                    <h4>Project Info :</h4>
                    <ul>
                        <li><span>Type : </span>Landing page</li>
                        <li><span>Tools : </span>HTML, CSS, SASS, npm, Photoshop, Illustrator</li>
                        <li><span>Web : </span><a href="https://anu-portfolio.github.io/yoga-dhyan/" target="_blank" rel="noopener noreferrer">www.yoga-dhyan.com</a></li>
                    </ul>
                </div>
            </div>

            )}
            
            <div className="project-slide-container">
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={webpage1} className="project-slide-img" alt="web-img" />
                <img src={webpage2} className="project-slide-img" alt="web-img" />   
                <img src={webpage3} className="project-slide-img" alt="web-img" />                   
            </Carousel>
            <div className="project-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
            </div>
        </div>
    )
}
export default Yogadhyan;