import React, { useState } from 'react';
import kdlogo from './images/portfolio/KD-logo.png';
import web from './images/portfolio/web.png';
import graphics from './images/portfolio/graphics.png';
import { IoIosArrowForward, IoIosArrowBack } from 'react-icons/io'
import { Link } from 'react-router-dom'

const images = [
    {
        id: 1,
        img: web,
        name: 'Web Applications',
        url_name: 'web-development-projects'
    },
    {
        id: 2,
        img: graphics,
        name: 'Graphic Designs',
        url_name: 'graphic-design-projects'
    },
    {
        id: 3,
        img: kdlogo,
        name: 'Internship Projects',
        url_name: 'internship-projects'
    }
];
const Portfolio = () => {
    const [index, setIndex] = useState(0);

    const prevSlide = () => {
        setIndex((oldIndex) => {
            let index = oldIndex - 1;
            if (index < 0) {
                index = images.length - 1;
            }
            return index;
        })
    }

    const nextSlide = () => {
        setIndex((oldIndex) => {
            let index = oldIndex + 1;
            if (index > images.length - 1) {
                index = 0;
            }
            return index;
        })
    }

    return (
        <div className="portfolio-container">
            <div className="portfolio-smallscreen">
                <div className="portfolio-item">
                    <Link to='/portfolio/web-development-projects' className="portfolio-card">
                        <img src={web} className="portfolio-card-img" alt="Web Applications" />
                        <p>Web Applications</p>
                    </Link>
                </div>
                <div className="portfolio-item">
                    <Link to='/portfolio/graphic-design-projects' className="portfolio-card">
                        <img src={graphics} className="portfolio-card-img" alt="Graphic Designs" />
                        <p>Graphic Designs</p>
                    </Link>
                </div>
                <div className="portfolio-item">
                    <Link to='/portfolio/internship-projects' className="portfolio-card">
                        <img src={kdlogo} className="portfolio-card-img" alt="Internship Projects" />
                        <p>Internship Projects</p>
                    </Link>
                </div>
            </div>
            <div className="portfolio-carousel">
                {images.map((image, imgIndex) => {
                    const { id, img, name, url_name} = image;

                    let position = "portfolio-nextSlide";

                    if (imgIndex === index) {
                        position = "portfolio-activeSlide";
                    }
                    if (imgIndex === index - 1 || (index === 0 && imgIndex === images.length - 1)) {
                        position = "portfolio-lastSlide";
                    }
                    return (
                        <article key={id} className={position}>
                            <Link to={`/portfolio/${url_name}`} className="portfolio-card">
                                <img src={img} className="portfolio-card-img" alt={name} />
                                <p>{name}</p>
                            </Link>
                        </article>
                    )
                })}
                <button className="regular-btn portfolio-prev-btn" onClick={prevSlide}><IoIosArrowBack className='testimonial-btn-icons' /></button>
                <button className="regular-btn portfolio-next-btn" onClick={nextSlide}><IoIosArrowForward className='testimonial-btn-icons' /></button>
            </div>

        </div >
    )
}
export default Portfolio;
