import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {TiPlus,TiMinus} from 'react-icons/ti'
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import webpage1 from '../images/portfolio/web/screenshots/kasial/page1.png'
import webpage2 from '../images/portfolio/web/screenshots/kasial/page2.png'
import webpage3 from '../images/portfolio/web/screenshots/kasial/page3.png'
import webpage4 from '../images/portfolio/web/screenshots/kasial/page4.png'
import webpage5 from '../images/portfolio/web/screenshots/kasial/page5.png'
import webpage6 from '../images/portfolio/web/screenshots/kasial/page6.png'
import webpage7 from '../images/portfolio/web/screenshots/kasial/page7.png'
import webpage8 from '../images/portfolio/web/screenshots/kasial/page8.png'
import webpage9 from '../images/portfolio/web/screenshots/kasial/page9.png'
import webpage10 from '../images/portfolio/web/screenshots/kasial/page10.png'
import webpage11 from '../images/portfolio/web/screenshots/kasial/page11.png'
import webpage12 from '../images/portfolio/web/screenshots/kasial/page12.png'
import webpage13 from '../images/portfolio/web/screenshots/kasial/page13.png'
import webpage14 from '../images/portfolio/web/screenshots/kasial/page14.png'
import webpage15 from '../images/portfolio/web/screenshots/kasial/page15.png'
import webpage16 from '../images/portfolio/web/screenshots/kasial/page16.png'
import webpage17 from '../images/portfolio/web/screenshots/kasial/page17.png'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

const Kasial = () => {

  const [showInfo, setShowInfo] = useState(false);
  const [index,setIndex] = useState(0);
  const totalSlide = 17;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }

    return (
        <div className="web-project-container">
            <header className="web-btn-container">
                <button className="web-project-btn regular-btn" onClick={()=>setShowInfo(!showInfo)}>
                    <span>Project Details</span>
                    {showInfo ? <TiMinus className="web-project-btn-icon" /> : <TiPlus className="web-project-btn-icon" />}
                </button>
                <Link to ='/portfolio/web-development-projects' className="web-close-btn regular-btn"><span>&times;</span></Link>
            </header>
            {showInfo && (
                <div className="project-details-box">
                    <div className="project-brief">
                    <h4>Project Brief :</h4>
                    <p>Developed an user-friendly responsive website and designed a unique logo for a local makeup artist 'Kasial'. 
                      It features with home, about, services, shop and contact pages. Header contains business logo, navigation bar and a button which takes user 
                      to the appointment scheduling form. Footer contains the newsletter signup and the social page links. Header and Footer are displayed to be fixed at all pages.
                      Home page displays highlighted services and offers that the business provides, their location and packages in icons, direct link to
                      their services pages, the makeup artist's portfolio. About page talks about the makeup artist, her experience, her achivements and client's
                      testimonials in a slider. Services page displays all the types of services the business offers which individually links to separate services page
                      which describes about the particular services. Shop page displays all the products that the business is marketing including filter option to 
                      display the products based on its type. It allows user to add the products to the cart, edit the count of the products and delete the product.
                      It further leads to the login/register page where user must login or create account and then go for checkout, there it asks for payment option using accordion 
                      and final order placement.
                    </p>
                </div>
                <div className="project-info">
                    <h4>Project Info :</h4>
                    <ul>
                        <li><span>Type : </span>E-Commerce Website</li>
                        <li><span>Tools : </span>HTML, CSS, Javascript, Bootstrap, Sass, Photoshop, Illustrator</li>
                        <li><span>Web : </span><a href="https://anu-portfolio.github.io/kasial/" target="_blank" rel="noopener noreferrer">www.kasial-makeup-artistry.com</a></li>
                    </ul>
                </div>
            </div>

            )}
            
            <div className="project-slide-container">
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={webpage1} className="project-slide-img" alt="web-img" />
                <img src={webpage16} className="project-slide-img" alt="web-img" />
                <img src={webpage2} className="project-slide-img" alt="web-img" />                    
                <img src={webpage3} className="project-slide-img" alt="web-img" />
                <img src={webpage4} className="project-slide-img" alt="web-img" />
                <img src={webpage5} className="project-slide-img" alt="web-img" />
                <img src={webpage6} className="project-slide-img" alt="web-img" />
                <img src={webpage7} className="project-slide-img" alt="web-img" />
                <img src={webpage15} className="project-slide-img" alt="web-img" /> 
                <img src={webpage8} className="project-slide-img" alt="web-img" />                
                <img src={webpage9} className="project-slide-img" alt="web-img" />
                <img src={webpage10} className="project-slide-img" alt="web-img" />
                <img src={webpage11} className="project-slide-img" alt="web-img" />
                <img src={webpage12} className="project-slide-img" alt="web-img" />                
                <img src={webpage13} className="project-slide-img" alt="web-img" />
                <img src={webpage17} className="project-slide-img" alt="web-img" />
                <img src={webpage14} className="project-slide-img" alt="web-img" />                               
            </Carousel>
            <div className="project-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
            </div>
        </div>
    )
}
export default Kasial;

                          