import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import Brief from './internship/Brief'
import Branding from './internship/Branding'
import Illustration from './internship/Illustration'
import Event from './internship/Event'
import Campaign from './internship/Campaign'

const Internship = () => {
    const [showBrief, setShowBrief] = useState(true);
    const [showCampaign,setShowCampaign] = useState(false);
    const [showEvent,setShowEvent] = useState(false);
    const [showBranding,setShowBranding] = useState(false);
    const [showIllustration,setShowIllustration] = useState(false);

    const displayBrief = () => {        
        setShowEvent(false);
        setShowBranding(false);
        setShowIllustration(false);
        setShowCampaign(false);
        setShowBrief(true);
    }

    const displayCampaign = () => {
        setShowBrief(false);
        setShowEvent(false);
        setShowBranding(false);
        setShowIllustration(false);
        setShowCampaign(true);
    }

    const displayBranding = () => {
        setShowBrief(false);
        setShowCampaign(false);
        setShowEvent(false);
        setShowIllustration(false);
        setShowBranding(true);
    }

    const displayEvent = (e) => {
        setShowBrief(false);
        setShowCampaign(false);
        setShowBranding(false);
        setShowIllustration(false);
        setShowEvent(true);
    }

    const displayIllustration = () => {
        setShowBrief(false);
        setShowCampaign(false);
        setShowEvent(false);
        setShowBranding(false);
        setShowIllustration(true);
    }

    return (
        <div className="internship-container">
            <div className="internship-link-container">
                <button className={`${showBrief ? "int-projects-link-active":"int-projects-link"}`} onClick={displayBrief}><span>Internship Brief</span></button>  
                <button className={`${showCampaign ? "int-projects-link-active":"int-projects-link"}`} onClick={displayCampaign}><span>Event Campaign</span></button>           
                <button className={`${showEvent ? "int-projects-link-active":"int-projects-link"}`} onClick={displayEvent}><span>Day of Event</span></button>
                <button className={`${showBranding ? "int-projects-link-active":"int-projects-link"}`} onClick={displayBranding}><span>Branding</span></button>
                <button className={`${showIllustration ? "int-projects-link-active":"int-projects-link"}`} onClick={displayIllustration}><span>Illustration</span></button>
            </div>

            <div className="internship-item-container">
                {showBrief && <Brief /> }
                {showCampaign && <Campaign /> }
                {showEvent && <Event /> }
                {showIllustration && <Illustration /> }
                {showBranding && <Branding /> }     
                <Link to ='/portfolio' className="internship-close-btn regular-btn"><span>&times;</span></Link>          
            </div>
        </div>
    )
}
export default Internship;