import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {TiPlus,TiMinus} from 'react-icons/ti'
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import webpage1 from '../images/portfolio/web/screenshots/itsquiztime/page1.png'
import webpage2 from '../images/portfolio/web/screenshots/itsquiztime/page2.png'
import webpage3 from '../images/portfolio/web/screenshots/itsquiztime/page3.png'
import webpage4 from '../images/portfolio/web/screenshots/itsquiztime/page4.png'
import webpage5 from '../images/portfolio/web/screenshots/itsquiztime/page5.png'
import webpage6 from '../images/portfolio/web/screenshots/itsquiztime/page6.png'
import webpage7 from '../images/portfolio/web/screenshots/itsquiztime/page7.png'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

const Itsquiztime = () => {

  const [showInfo, setShowInfo] = useState(false);
  const [index,setIndex] = useState(0);
  const totalSlide = 7;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }

    return (
        <div className="web-project-container">
            <header className="web-btn-container">
                <button className="web-project-btn regular-btn" onClick={()=>setShowInfo(!showInfo)}>
                    <span>Project Details</span>
                    {showInfo ? <TiMinus className="web-project-btn-icon" /> : <TiPlus className="web-project-btn-icon" />}
                </button>
                <Link to ='/portfolio/web-development-projects' className="web-close-btn regular-btn"><span>&times;</span></Link>
            </header>
            {showInfo && (
                <div className="project-details-box">
                    <div className="project-brief">
                    <h4>Project Brief :</h4>
                    <p>
                      This Quiz application will connect to external API and allows user to customize the quiz based on category, no of questions and difficulty level. 
                      If the user chooses the correct answer a modal pops up saying correct answer and allows user to go to next question, on the other way it says wrong answer 
                      and displays the correct answer. There is also an option for the user to skip the question. At the end of the quiz the result modal pops up showing 
                      total no. of questions answered, total no. of questions skipped, total no. of correct answers and total percentage of score. This also allows user to start
                      the quiz again or end the quiz and returns to the home page. Logo is designed uniquely for this application.
                    </p>
                </div>
                <div className="project-info">
                    <h4>Project Info :</h4>
                    <ul>
                        <li><span>Type : </span>Quiz Application</li>
                        <li><span>API : </span>'https://opentdb.com/api.php?'</li>
                        <li><span>Tools : </span>HTML, CSS, npm, Javascript, React, Photoshop, Illustrator</li>
                        <li><span>Web : </span><a href="https://anu-portfolio.github.io/itsQuiztime/" target="_blank" rel="noopener noreferrer">www.its-Quiz-time.com</a></li>
                    </ul>
                </div>
            </div>

            )}
            
            <div className="project-slide-container">
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={webpage1} className="project-slide-img" alt="web-img" />
                <img src={webpage2} className="project-slide-img" alt="web-img" />   
                <img src={webpage3} className="project-slide-img" alt="web-img" />
                <img src={webpage4} className="project-slide-img" alt="web-img" />
                <img src={webpage5} className="project-slide-img" alt="web-img" />
                <img src={webpage6} className="project-slide-img" alt="web-img" />  
                <img src={webpage7} className="project-slide-img" alt="web-img" />                     
            </Carousel>
            <div className="project-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
            </div>
        </div>
    )
}
export default Itsquiztime;

                          