import React from 'react';
import Navbar from './Navbar';
import Home from './Home';
import About from './About';
import Services from './Services';
import Testimonials from './Testimonials';
import Portfolio from './Portfolio';
import Web from './Web';
import Graphics from './Graphics';
import Internship from './Internship';
import Contact from './Contact';
import Resume from './Resume';
import Error from './Error';
import Kasial from './web/Kasial';
import Clickarecipe from './web/Clickarecipe';
import Itsquiztime from './web/Itsquiztime';
import Yogadhyan from './web/Yogadhyan';
import Vactrak from './web/Vactrak';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

function App() {

  return (
    <Router>
      <Navbar />
      <Switch>
        <Route exact path='/'><Home /></Route>
        <Route exact path='/about'><About /></Route>
        <Route exact path='/services'><Services /></Route>
        <Route exact path='/portfolio'><Portfolio /></Route>
        <Route exact path='/portfolio/web-development-projects'><Web /></Route>
        <Route exact path='/portfolio/web-development-projects/kasial-makeup-artistry'><Kasial /></Route>
        <Route exact path='/portfolio/web-development-projects/clickArecipe'><Clickarecipe /></Route>
        <Route exact path='/portfolio/web-development-projects/itsQuiztime'><Itsquiztime /></Route>
        <Route exact path='/portfolio/web-development-projects/YogaDhyan'><Yogadhyan /></Route>
        <Route exact path='/portfolio/web-development-projects/VacTrak'><Vactrak /></Route>
        <Route exact path='/portfolio/graphic-design-projects'><Graphics /></Route>
        <Route exact path='/portfolio/internship-projects'><Internship /></Route>
        <Route exact path='/testimonials'><Testimonials /></Route>
        <Route exact path='/resume'><Resume /></Route>
        <Route exact path='/contact'><Contact /></Route>
        <Route path='*'><Error /></Route>
      </Switch>
    </Router>
  )
}

export default App;
