import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {TiPlus,TiMinus} from 'react-icons/ti'
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import webpage1 from '../images/portfolio/web/screenshots/clickarecipe/page1.png'
import webpage2 from '../images/portfolio/web/screenshots/clickarecipe/page2.png'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

const Clickarecipe = () => {

  const [showInfo, setShowInfo] = useState(false);
  const [index,setIndex] = useState(0);
  const totalSlide = 2;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }

    return (
        <div className="web-project-container">
            <header className="web-btn-container">
                <button className="web-project-btn regular-btn" onClick={()=>setShowInfo(!showInfo)}>
                    <span>Project Details</span>
                    {showInfo ? <TiMinus className="web-project-btn-icon" /> : <TiPlus className="web-project-btn-icon" />}
                </button>
                <Link to ='/portfolio/web-development-projects' className="web-close-btn regular-btn"><span>&times;</span></Link>
            </header>
            {showInfo && (
                <div className="project-details-box">
                    <div className="project-brief">
                    <h4>Project Brief :</h4>
                    <p>
                      Click-A-Recipe is a recipe searching website where the front end interface connects to the external API 
                      and provide the requested content to the user. It features a home page with list of trending recipes displayed in an image slider carousel and a search box. 
                      Once the user enters a valid food item name (e.g. Pizza) the page will get loaded with the list of pizza recipes with pagination. 
                      The user can click on to the required pizza and get that pizza recipe loaded with its image, 
                      instructions to cook and list of its ingredients with quantity per servings. The no. of servings can be increased or decreased, as per the 
                      servings count the quantity of ingredients will be changed. Also the user can click on to the favorite icon and add that particular recipe to the 'my recipe collection'. 
                      This recipe collection will be stored in local storage and can be retrieved later. Designed a unique logo for this web application.
                    </p>
                </div>
                <div className="project-info">
                    <h4>Project Info :</h4>
                    <ul>
                        <li><span>Type : </span>Recipe Website</li>
                        <li><span>API : </span>"spoonacular-recipe-food-nutrition-v1.p.rapidapi.com"</li>
                        <li><span>Tools : </span>HTML, CSS, Sass, npm, Javascript, jQuery, Photoshop, Illustrator</li>
                        <li><span>Web : </span><a href="https://anu-portfolio.github.io/clickArecipe/" target="_blank" rel="noopener noreferrer">www.click-A-recipe.com</a></li>
                    </ul>
                </div>
            </div>

            )}
            
            <div className="project-slide-container">
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={webpage1} className="project-slide-img" alt="web-img" />
                <img src={webpage2} className="project-slide-img" alt="web-img" />                    
            </Carousel>
            <div className="project-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
            </div>
        </div>
    )
}
export default Clickarecipe;

                          