import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {TiPlus,TiMinus} from 'react-icons/ti'
import { GoChevronRight, GoChevronLeft } from 'react-icons/go';
import webpage1 from '../images/portfolio/web/screenshots/vactrak/page1.png'
import webpage2 from '../images/portfolio/web/screenshots/vactrak/page2.png'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

const Vactrak = () => {

  const [showInfo, setShowInfo] = useState(false);
  const [index,setIndex] = useState(0);
  const totalSlide = 2;

  const prevSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex - 1;
      if (index < 0){
        index = totalSlide-1;
      }
      return index;
    })
  }
    const nextSlide = () => {
    setIndex((oldIndex) => {
      let index = oldIndex + 1;
      if (index > totalSlide-1){
        index = 0;
      }
      return index;
    })
  }

    return (
        <div className="web-project-container">
            <header className="web-btn-container">
                <button className="web-project-btn regular-btn" onClick={()=>setShowInfo(!showInfo)}>
                    <span>Project Details</span>
                    {showInfo ? <TiMinus className="web-project-btn-icon" /> : <TiPlus className="web-project-btn-icon" />}
                </button>
                <Link to ='/portfolio/web-development-projects' className="web-close-btn regular-btn"><span>&times;</span></Link>
            </header>
            {showInfo && (
                <div className="project-details-box">
                    <div className="project-brief">
                    <h4>Project Brief :</h4>
                    <p>
                      This website is developed to track the COVID-19 vaccination progress in United States, overall data and individual state data by entering 
                      the state name in the search box. If the user enters the invalid state name or null it throws an error asking to enter a valid state name. 
                      The front end interface connects to the CDC API and get the data on total vaccine doses, total people vaccinated, vaccine initiated 
                      vs fully vaccinated, vaccine distributed and administered by age and vaccine type, vaccination completed by series type. 
                      Data retrieved from the CDC database is displayed using FusionCharts. Designed unique logo for this website. 
                    </p>
                </div>
                <div className="project-info">
                    <h4>Project Info :</h4>
                    <ul>
                        <li><span>Type : </span>Informational website</li>
                        <li><span>API : </span>'https://covid.cdc.gov/covid-data-tracker/COVIDData/getAjaxData?id=vaccination_data'</li>
                        <li><span>Tools : </span>HTML, CSS, npm, Javascript, React, Photoshop, Illustrator</li>
                        <li><span>Web : </span><a href="https://anu-portfolio.github.io/VacTrak/" target="_blank" rel="noopener noreferrer">www.vactrak.com</a></li>
                    </ul>
                </div>
            </div>

            )}
            
            <div className="project-slide-container">
            <Carousel showThumbs={false} dynamicHeight={true} showArrows={false}
            statusFormatter={(current, total) => `${current} / ${total}`} 
            selectedItem={index} showStatus={false}>
                <img src={webpage1} className="project-slide-img" alt="web-img" />
                <img src={webpage2} className="project-slide-img" alt="web-img" />                    
            </Carousel>
            <div className="project-btn-container">
            <button className="project-prev-btn regular-btn" onClick={prevSlide}>
                <GoChevronLeft />
            </button>
            <span className="slide-status">{index+1} / {totalSlide}</span>
            <button className="project-next-btn regular-btn" onClick={nextSlide}>
                <GoChevronRight />
            </button>
            </div>
            </div>
        </div>
    )
}
export default Vactrak;

                          